.catalog-more {
  font-size: 12px;
  line-height: 16px;
  color: $color-gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 210px;
  padding-top: 56px;
  position: relative;
}

.catalog-more__heading {
  color: $color-black;
  font-size: 16px;
  margin-top: 24px;
  margin-bottom: 0;
}

.catalog-more__heading::before {
  position: absolute;
  content: " ";
  top: 26px;
  left: 125px;
  height: 30px;
  width: 30px;
  background-image: url("../../img/icon-plus-mobile.svg");
  background-repeat: no-repeat;
  opacity: 0.2;
}

.catalog-more__all {
  background-color: $color-lightgray;
  color: $color-gray;
}

.catalog-more__all:hover {
  background-color: $color-gray-button;
}

.catalog-more__all:active {
  color: rgba($color-gray, 0.3);
}

.catalog-more__text {
  margin-top: 8px;
  margin-bottom: 20px;
}

@media (min-width: $tablet-width) {
  .catalog-more {
    border-bottom: none;
  }

  .catalog-more__image {
    width: 60px;
    height: 60px;
  }

  .catalog-more__text {
    font-size: 16px;
    line-height: 18px;
    width: 200px;
    text-align: center;
    margin: 0 auto;
    margin-top: 28px;
    margin-bottom: 84px;
  }

  .catalog-more__all {
    width: 100%;
    font-size: 16px;
    padding: 10px;
  }

  .catalog-more__heading {
    font-size: 20px;
    max-width: 150px;
    text-align: center;
    margin-top: 70px;
    margin-bottom: 0;
  }

  .catalog-more__heading::before {
    position: absolute;
    content: " ";
    top: 16px;
    left: 96px;

    height: 60px;
    width: 60px;

    background-image: url("../../img/icon-plus.svg");
    background-repeat: no-repeat;
    opacity: 0.2;
  }
}

@media (min-width: $desktop-width) {
  .catalog-more__heading {
    margin-top: 71px;
    margin-bottom: 0;
  }

  .catalog-more__heading::before {
    position: absolute;
    content: " ";
    top: 18px;
    left: 56px;

    height: 60px;
    width: 60px;

    background-image: url("../../img/icon-plus.svg");
    background-repeat: no-repeat;
    opacity: 0.2;
  }

  .catalog-more__text {
    width: auto;
    margin-top: 26px;
    margin-bottom: 86px;
  }
}
